* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

html{
  height: 100vh;
}

body {
  background-image: url('./bg.jpg');
  background-repeat: no-repeat;
  overflow: hidden;
  background-size: cover;
}


.todo-app {
  display: flex;
  flex-direction: column;
  justify-content: start;
  width: 53%;
  height: 80vh;
  background: #272930;
  text-align: center;
  margin: 8rem auto;
  border-radius: 10px;
  padding-bottom: 2rem;
  overflow-y: scroll;
}

::-webkit-scrollbar{
  border-radius: 2rem;
  width: .3rem;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 2rem;
  margin: .5rem 0 .5rem 0;

}

::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 2rem;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
  border-radius: 2rem;
}

.todo-arange{
  display: flex;
  flex-wrap: wrap;
}

h1 {
  margin: 2rem 0;
  color: #fff;
  font-size: 1.5rem;
}

.complete {
  text-decoration: line-through;
  opacity: 0.5;
}

.todo-form {
  margin: 0 auto 2.188rem auto;
}


.todo-input {
  padding: 0.875rem 2rem 0.875rem 1rem;
  border-radius: 4px 0 0 4px;
  border: 0.125rem solid #119da7;
  outline: none;
  width: 70%;
  background: transparent;
  color: #fff;
  text-overflow: ellipsis;
  overflow: hidden;
}

.todo-input::placeholder {
  color: #e2e2e2;
}

.todo-button {
  padding: 1rem;
  border: none;
  border-radius: 0 0.25rem 0.25rem 0;
  cursor: pointer;
  outline: none;
  background: linear-gradient(
    90deg,
    rgb(11, 92, 112) 0%,
    rgb(14, 175, 187) 100%
  );
  color: #fff;
  text-transform: capitalize;
}

.todo-input.edit {
  border: 2px solid #139e48;
  height: 8rem;
  width: 90%;
}

.todo-button.edit {
  background: linear-gradient(
    90deg,
    rgb(45, 235, 55) 0%,
    rgb(19, 187, 109) 100%
  );
  padding: 16px 22px;
  margin-top: 2rem;
  border-radius: 4px;
  width: 70%;
}

.todo-container {
  display: flex;
  flex-direction: row;
  position: relative;
}

.todo-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1rem;
  color: #fff;
  background: linear-gradient(
    90deg, 
    rgb(222, 98, 98) 0%, 
    rgb(255, 184, 140) 100%
  );
  padding: 1rem;
  border-radius: 5px;
  width: 300px;
  height: 200px;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: space-evenly;
  justify-content: center;
  
}

.todo-row:nth-child(4n + 1) {
  background: linear-gradient(
    90deg,
    rgb(66, 39, 90) 0%,
    rgb(115, 75, 109) 100%
  );
}

.todo-row:nth-child(4n + 2) {
  background: linear-gradient(
    90deg,
    rgb(33, 147, 176) 0%,
    rgb(109, 213, 237) 100%
  );
}

.todo-row:nth-child(4n + 3) {
  background: linear-gradient(
    90deg,
    rgb(255, 175, 189) 0%,
    rgb(255, 195, 160) 100%
  );
}

.todo-text {
  width: 100%;
  white-space: pre-wrap;
  word-wrap: break-word;
  font-size: 1.5rem;
}

.icons {
  display: flex;
  align-items: center;
  font-size: 1.5rem;
  cursor: pointer;
}

.delete-icon, .edit-icon {
  color: #fff;
}

.checkbox {
  height: 1.5rem;
}

@media (max-width: 768px) {
  body{
    width: 100%;
    height: 70vh;
    overflow: hidden;
  }

  h1{
    font-size: 1.5rem;
    word-wrap: break-word;
    white-space: pre-wrap;
    margin: 2rem 1.25rem;
  }

  .todo-app{
    width: 90%;
    margin-top: 3.125rem;
    overflow-y: scroll;
  }

  .todo-row{
    margin: 10px auto;
    width: 90%;
  }

  .todo-text {
    width: 100%;
    font-size: 1rem;
    word-wrap: break-word;
    white-space: pre-wrap;
  }
  
  .icons {
    display: flex;
    align-items: center;
    font-size: 1.8rem;
    cursor: pointer;
    margin: 0.313rem;
  }
  
  .delete-icon, .edit-icon {
    color: #fff;
  }
  
  .checkbox {
    height: 1.8rem;
  }

  
}

